import { useState } from 'react';
import Layout from '../../components/layout/Layout';

interface PolicyProposal {
  id: string;
  title: string;
  description: string;
  category: string;
  region: string;
  proposer: string;
  votes: number;
  status: 'active' | 'completed' | 'pending';
  createdAt: string;
  targetPopulation: number;
  expectedImpact: string;
}

const PolicyVoting = () => {
  const [activeTab, setActiveTab] = useState<'proposals' | 'create'>('proposals');
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [votedProposals, setVotedProposals] = useState<Set<string>>(new Set());

  const [newProposal, setNewProposal] = useState({
    title: '',
    description: '',
    category: '',
    region: '',
    targetPopulation: '',
    expectedImpact: ''
  });

  const categories = [
    { id: 'all', name: '' },
    { id: 'exercise', name: '' },
    { id: 'nutrition', name: '/' },
    { id: 'environment', name: '' },
    { id: 'mental', name: '' },
    { id: 'community', name: '' }
  ];

  const mockProposals: PolicyProposal[] = [
    {
      id: '1',
      title: '     ',
      description: '                 .',
      category: 'exercise',
      region: ' ',
      proposer: '',
      votes: 247,
      status: 'active',
      createdAt: '2024-01-15',
      targetPopulation: 15000,
      expectedImpact: ' 2.3%  '
    },
    {
      id: '2',
      title: '   ',
      description: '              .',
      category: 'nutrition',
      region: ' ',
      proposer: '',
      votes: 189,
      status: 'active',
      createdAt: '2024-01-12',
      targetPopulation: 12000,
      expectedImpact: ' 1.8%  '
    },
    {
      id: '3',
      title: '   ',
      description: '            .',
      category: 'mental',
      region: ' ',
      proposer: '',
      votes: 156,
      status: 'active',
      createdAt: '2024-01-10',
      targetPopulation: 8500,
      expectedImpact: '  1.5%  '
    },
    {
      id: '4',
      title: '     ',
      description: '            .',
      category: 'environment',
      region: ' ',
      proposer: '',
      votes: 203,
      status: 'completed',
      createdAt: '2024-01-08',
      targetPopulation: 18000,
      expectedImpact: '  2.1% '
    }
  ];

  const filteredProposals = selectedCategory === 'all' 
    ? mockProposals 
    : mockProposals.filter(p => p.category === selectedCategory);

  const handleVote = (proposalId: string) => {
    if (votedProposals.has(proposalId)) {
      alert('  .');
      return;
    }
    
    setVotedProposals(prev => new Set([...prev, proposalId]));
    alert(' !');
  };

  const handleSubmitProposal = () => {
    if (!newProposal.title || !newProposal.description || !newProposal.category || !newProposal.region) {
      alert('   .');
      return;
    }

    alert('   !    .');
    setNewProposal({
      title: '',
      description: '',
      category: '',
      region: '',
      targetPopulation: '',
      expectedImpact: ''
    });
    setActiveTab('proposals');
  };

  const getStatusBadge = (status: string) => {
    const badges = {
      active: { text: ' ', color: 'bg-green-100 text-green-800' },
      completed: { text: ' ', color: 'bg-blue-100 text-blue-800' },
      pending: { text: '', color: 'bg-yellow-100 text-yellow-800' }
    };
    return badges[status as keyof typeof badges] || badges.pending;
  };

  const getCategoryName = (categoryId: string) => {
    return categories.find(c => c.id === categoryId)?.name || categoryId;
  };

  return (
    <Layout>
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-4">    </h1>
          <p className="text-lg text-gray-600">
                      .
          </p>
        </div>

        {/* Tabs */}
        <div className="mb-8">
          <div className="border-b border-gray-200">
            <nav className="-mb-px flex space-x-8">
              <button
                onClick={() => setActiveTab('proposals')}
                className={`py-2 px-1 border-b-2 font-medium text-sm whitespace-nowrap cursor-pointer ${
                  activeTab === 'proposals'
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                <i className="ri-vote-line mr-2"></i>
                 
              </button>
              <button
                onClick={() => setActiveTab('create')}
                className={`py-2 px-1 border-b-2 font-medium text-sm whitespace-nowrap cursor-pointer ${
                  activeTab === 'create'
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                <i className="ri-add-line mr-2"></i>
                 
              </button>
            </nav>
          </div>
        </div>

        {activeTab === 'proposals' ? (
          <div>
            {/* Category Filter */}
            <div className="mb-6">
              <div className="flex flex-wrap gap-2">
                {categories.map((category) => (
                  <button
                    key={category.id}
                    onClick={() => setSelectedCategory(category.id)}
                    className={`px-4 py-2 rounded-full text-sm font-medium transition-colors whitespace-nowrap cursor-pointer ${
                      selectedCategory === category.id
                        ? 'bg-blue-600 text-white'
                        : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                    }`}
                  >
                    {category.name}
                  </button>
                ))}
              </div>
            </div>

            {/* Statistics */}
            <div className="grid md:grid-cols-4 gap-4 mb-8">
              <div className="bg-white rounded-lg shadow p-4 text-center">
                <div className="text-2xl font-bold text-blue-600">{mockProposals.length}</div>
                <div className="text-sm text-gray-600">  </div>
              </div>
              <div className="bg-white rounded-lg shadow p-4 text-center">
                <div className="text-2xl font-bold text-green-600">
                  {mockProposals.filter(p => p.status === 'active').length}
                </div>
                <div className="text-sm text-gray-600"> </div>
              </div>
              <div className="bg-white rounded-lg shadow p-4 text-center">
                <div className="text-2xl font-bold text-purple-600">
                  {mockProposals.reduce((sum, p) => sum + p.votes, 0)}
                </div>
                <div className="text-sm text-gray-600">  </div>
              </div>
              <div className="bg-white rounded-lg shadow p-4 text-center">
                <div className="text-2xl font-bold text-orange-600">
                  {mockProposals.filter(p => p.status === 'completed').length}
                </div>
                <div className="text-sm text-gray-600"> </div>
              </div>
            </div>

            {/* Proposals List */}
            <div className="space-y-6">
              {filteredProposals.map((proposal) => (
                <div key={proposal.id} className="bg-white rounded-lg shadow-lg p-6">
                  <div className="flex items-start justify-between mb-4">
                    <div className="flex-1">
                      <div className="flex items-center space-x-3 mb-2">
                        <h3 className="text-xl font-bold text-gray-900">{proposal.title}</h3>
                        <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusBadge(proposal.status).color}`}>
                          {getStatusBadge(proposal.status).text}
                        </span>
                      </div>
                      <div className="flex items-center space-x-4 text-sm text-gray-600 mb-3">
                        <span>
                          <i className="ri-map-pin-line mr-1"></i>
                          {proposal.region}
                        </span>
                        <span>
                          <i className="ri-price-tag-3-line mr-1"></i>
                          {getCategoryName(proposal.category)}
                        </span>
                        <span>
                          <i className="ri-user-line mr-1"></i>
                          {proposal.proposer}
                        </span>
                        <span>
                          <i className="ri-calendar-line mr-1"></i>
                          {proposal.createdAt}
                        </span>
                      </div>
                    </div>
                    <div className="text-right">
                      <div className="text-2xl font-bold text-blue-600">{proposal.votes}</div>
                      <div className="text-sm text-gray-600"></div>
                    </div>
                  </div>

                  <p className="text-gray-700 mb-4 leading-relaxed">{proposal.description}</p>

                  <div className="grid md:grid-cols-2 gap-4 mb-4">
                    <div className="bg-blue-50 rounded-lg p-3">
                      <div className="text-sm text-blue-600 font-medium mb-1"> </div>
                      <div className="text-lg font-bold text-blue-800">
                        {proposal.targetPopulation.toLocaleString()}
                      </div>
                    </div>
                    <div className="bg-green-50 rounded-lg p-3">
                      <div className="text-sm text-green-600 font-medium mb-1"> </div>
                      <div className="text-lg font-bold text-green-800">
                        {proposal.expectedImpact}
                      </div>
                    </div>
                  </div>

                  {proposal.status === 'active' && (
                    <div className="flex items-center justify-between">
                      <div className="text-sm text-gray-600">
                             ?
                      </div>
                      <button
                        onClick={() => handleVote(proposal.id)}
                        disabled={votedProposals.has(proposal.id)}
                        className={`px-6 py-2 rounded-md font-medium transition-colors whitespace-nowrap cursor-pointer ${
                          votedProposals.has(proposal.id)
                            ? 'bg-gray-300 text-gray-500 cursor-not-allowed'
                            : 'bg-blue-600 text-white hover:bg-blue-700'
                        }`}
                      >
                        <i className="ri-thumb-up-line mr-2"></i>
                        {votedProposals.has(proposal.id) ? ' ' : ' '}
                      </button>
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>
        ) : (
          <div className="bg-white rounded-lg shadow-lg p-6">
            <h2 className="text-xl font-bold text-gray-900 mb-6">  </h2>
            
            <div className="space-y-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                    *
                </label>
                <input
                  type="text"
                  value={newProposal.title}
                  onChange={(e) => setNewProposal({...newProposal, title: e.target.value})}
                  placeholder="    "
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                    *
                </label>
                <textarea
                  value={newProposal.description}
                  onChange={(e) => setNewProposal({...newProposal, description: e.target.value})}
                  placeholder="  ,  ,     "
                  rows={4}
                  maxLength={500}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
                <div className="text-sm text-gray-500 mt-1">
                  {newProposal.description.length}/500
                </div>
              </div>

              <div className="grid md:grid-cols-2 gap-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                      *
                  </label>
                  <select
                    value={newProposal.category}
                    onChange={(e) => setNewProposal({...newProposal, category: e.target.value})}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  >
                    <option value=""> </option>
                    {categories.slice(1).map((category) => (
                      <option key={category.id} value={category.id}>
                        {category.name}
                      </option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                      *
                  </label>
                  <input
                    type="text"
                    value={newProposal.region}
                    onChange={(e) => setNewProposal({...newProposal, region: e.target.value})}
                    placeholder=":  "
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  />
                </div>
              </div>

              <div className="grid md:grid-cols-2 gap-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                       ()
                  </label>
                  <input
                    type="number"
                    value={newProposal.targetPopulation}
                    onChange={(e) => setNewProposal({...newProposal, targetPopulation: e.target.value})}
                    placeholder="     "
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     
                  </label>
                  <input
                    type="text"
                    value={newProposal.expectedImpact}
                    onChange={(e) => setNewProposal({...newProposal, expectedImpact: e.target.value})}
                    placeholder=":  2%  "
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  />
                </div>
              </div>

              <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
                <div className="flex items-start">
                  <i className="ri-information-line text-yellow-600 text-lg mr-3 mt-0.5"></i>
                  <div className="text-sm text-yellow-800">
                    <strong> :</strong>
                    <ul className="mt-2 space-y-1 list-disc list-inside">
                      <li>    </li>
                      <li>      </li>
                      <li>     </li>
                      <li>GWR      </li>
                    </ul>
                  </div>
                </div>
              </div>

              <div className="flex justify-end space-x-4">
                <button
                  onClick={() => setActiveTab('proposals')}
                  className="px-6 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors whitespace-nowrap cursor-pointer"
                >
                  
                </button>
                <button
                  onClick={handleSubmitProposal}
                  className="px-6 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors whitespace-nowrap cursor-pointer"
                >
                  <i className="ri-send-plane-line mr-2"></i>
                   
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </Layout>
  );
};

export default PolicyVoting;